 .why-choose-us-container {
     padding: 1.8em;
     background-color: #fff;
     /* border: 1px solid black; */
 }

 .why-choose-us-container h1 {
     font-size: 2.5em;
     font-family: "Poppins", sans-serif;
     font-weight: normal;
     text-align: center;
     margin-bottom: 1.25rem;
 }

 .why-choose-us-container > .services-grid {
     gap: 1em;
 }

 .why-choose-us-container > .services-grid > .service-card {
     letter-spacing: normal;
     width: 16.25em;
     padding: 1em;
     align-items: center;
     justify-content: center;
 }

 .why-choose-us-container > .services-grid > .service-card > img {
     width: 80px;
 }

 .why-choose-us-container > .services-grid > .service-card > h2 {
     margin-top: 0;
     margin-bottom: 0.5em;
 }

 .why-choose-us-container > .services-grid > .service-card > p {
     font-size: 0.65em;
     font-weight: 500;
     color: black
 }

 .services-container {
     padding: 1.25em;
     text-align: center;
 }

 .services-container h1 {
     width: fit-content;
     margin: 0.5em auto 0.8em 2em;
     font-family: 'Poppins', sans-serif;
     font-size: 2.73125em;
     font-weight: 600;
     font-stretch: normal;
     font-style: normal;
     line-height: 1.4;
     letter-spacing: normal;
     text-align: left;
     color: #000;
     /* border: 2px solid black; */
 }

 .services-grid {
     display: flex;
     flex-wrap: wrap;
     justify-content: center;
     gap: 3em;
     /* border: 1px solid black; */
     padding: 1.25em 1.2em;
 }



 .service-card {

     font-family: 'Poppins', sans-serif;
     font-stretch: normal;
     font-style: normal;
     line-height: 1;
     letter-spacing: -0.08rem;

     width: 20.33125em;
     display: flex;
     flex-wrap: wrap;
     flex-direction: column;

     padding: 1.5em;
     border-radius: 0.5em;
     border: solid 0.0625em #d9d9d9;
     background-color: #fff;
     box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
     cursor: pointer;
     transition: transform 0.3s;
 }

 .service-card:hover {
     transform: translateY(-10px);
 }

 .service-card img {
     width: 160px;
     flex-grow: 0;
     /* border: 1px solid black; */
 }

 .service-card h2 {
     font-size: 1.5em;
     font-weight: 600;
     margin: 0;
     margin-top: 0.6em;
     padding: 0.1em 0.01em;
     text-align: left;
     /* border: 1px solid black; */
     flex-grow: 0;
 }

 .service-card p {
     padding: 0.5em 0.01em;
     padding-top: 0.1em;
     margin-top: 0;
     font-size: 0.9em;
     letter-spacing: normal;
     line-height: normal;
     color: #666666;
     text-align: left;
     /* border: 1px solid black; */
 }

 @media (max-width: 580px) {
     .services-container {
         padding: 0.5em;
     }

     .services-container > h1 {
         margin: 0.5em auto;
     }

     .services-grid {
         flex-direction: column;
         align-items: center;
     }

     .service-card {
         width: 80%;
     }
 }

 @media (max-width: 400px) {
     .services-container h1 {
         font-size: 2em;
     }

     .services-grid {
         gap: 1rem;
         padding: 0.8em;
     }

     .service-card {
         width: 100%;
         padding: 0.8em
     }

     .service-card h2 {
         font-size: 1.5em;
     }

     .service-card p {
         font-size: 0.8em;
     }

     .service-card img {
         width: 100%;
     }

     .why-choose-us-container > h1 {
         font-size: 2em;
         margin-top: 0em;

     }

     .why-choose-us-container > .services-grid > .service-card {
         width: auto;
     }
 }